.calendar {
  padding: 15% #{$padding-width};
  margin: 0 auto;

  display: flex;
  flex-wrap: wrap;
  gap: $gap-width;

  width: calc(
    #{$total-columns} * #{$day-size} +
    (#{$total-columns} - 1) * #{$gap-width} +
    2 * #{$padding-width}
  );

  @each $day, $value in $days-of-week {
    &--start-day--#{$day}
    &__day:first-child {
      margin-left: (($day-size + $gap-width) * $value);
    }
  }

  @for $day from 28 through 31 {
    &--month-length--#{$day}
    &__day:nth-child(n + #{$day + 1}) {
      display: none;
    }
  }

  &__day {
    @include square;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;

    background-color: $bg-day-default;
    border: 1px solid black;
    transition: all 0.5s ease-in-out;

    &:hover {
      cursor: pointer;
      background-color: pink;
      transform: translateY(-20px);
    }

    @for $day from 1 through 31 {
      &:nth-child(#{$day})::before {
        content: "#{$day}";
        display: block;
        font-family: Arial, sans-serif;
        font-size: 30px;
      }
    }
  }
}
